table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 40%;
    margin: auto;
  }
  
  td, th {
    border: 1px solid #dddddd;
    text-align: center;
    padding: 8px;
  }
  
  tr:nth-child(even) {
    background-color: #dddddd;
  }

  h1 {
    text-align: center;
  }

  .pagination {
      margin:auto !important;
      text-align: center !important;
  }